import React from 'react';
import {Component} from 'react';
import Card from 'react-bootstrap/Card';
import CardDeck from 'react-bootstrap/CardDeck'
import Button from 'react-bootstrap/Button';

class Projects extends Component{

    render(){
        return(
            <div className="project_page">
                <CardDeck>
                <Card>
                    <Card.Body>
                    <Card.Title>Note: Projects will be added soon!</Card.Title>
                    <Card.Text>
                        My current proejct list had an issue with React. This page will get updated in the next month.
                        Thank you for being interested in my Projects!{' '}
                    </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <small className="text-muted">Last updated 2/25/2021</small>
                    </Card.Footer>
                </Card>
                <Card>
                    <Card.Body>
                    <Card.Title>Project Management System</Card.Title>
                    <Card.Text>
                        This system runs on Firebase and React and allows for students and professionals
                        to communicate with each other and have a place to manage the project status, and for
                        user groups that are create towards each organization.
                    </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <small className="text-muted">In Progress...</small>
                    </Card.Footer>
                </Card>
                </CardDeck>

            </div>
        )
    }
}


export default Projects;